.app {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  background-color: rgba(255, 255, 255, 1);
  color: rgba(0, 0, 0, 1);
  font-family: "Inter", "Arial", sans-serif;
  font-style: normal;
  font-size: 10px;
  min-width: 280px;
  margin: 0 auto;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.calendar {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  flex: 1 1;
  /* background-color: #72757a; */
  /* margin-inline: auto; */
}

.calendar__container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 500px;
  min-height: 500px;
}

.react-datepicker__month-container {
  width: 380px;
  font-family: "Inter", "Arial", sans-serif;
}

.react-datepicker__header .react-datepicker__current-month {
  font-size: 20px;
  font-weight: 500;
  text-transform: capitalize;
}

.react-datepicker__header .react-datepicker__day-name {
  font-size: 16px;
  padding: 8px 12px;
  margin: 0;
}

.react-datepicker__week .react-datepicker__day {
  font-size: 16px;
  padding: 8px 12px;
  margin: 0;
}

.react-datepicker .react-datepicker__navigation {
  width: 40px;
  height: 40px;
}

.react-datepicker__week .react-datepicker__day--today {
  font-weight: 500;
}

@media (max-width: 991px) {
  .calendar {
    flex-direction: column;
    justify-content: center;
    grid-gap: 4.24vh;
    gap: 4.24vh;
  }

  .calendar__container {
    margin-top: 20px;
    width: 270px;
    min-height: 270px;
  }

  .react-datepicker__month-container {
    width: 270px;
    font-family: "Inter", "Arial", sans-serif;
  }

  .react-datepicker__header .react-datepicker__current-month {
    font-size: 16px;
    font-weight: 500;
    text-transform: capitalize;
  }

  .react-datepicker__header .react-datepicker__day-name {
    font-size: 14px;
    padding: 1px 5px;
    margin: 0;
  }

  .react-datepicker__week .react-datepicker__day {
    font-size: 14px;
    padding: 3px 5px;
    margin: 0;
  }

  .react-datepicker .react-datepicker__navigation {
    width: 32px;
    height: 32px;
  }
}

@media (max-width: 540px) {
  .calendar {
    flex-direction: column;
    justify-content: center;
    grid-gap: 0;
    gap: 0;
  }

  .calendar__container {
    margin-top: 0;
    width: 300px;
    min-height: 230px;
  }

  .react-datepicker__month-container {
    width: 300px;
    font-family: "Inter", "Arial", sans-serif;
  }

  .react-datepicker__header .react-datepicker__current-month {
    font-size: 16px;
    font-weight: 500;
    text-transform: capitalize;
  }

  .react-datepicker__header .react-datepicker__day-name {
    font-size: 14px;
    padding: 0 7px;
    margin: 0;
  }

  .react-datepicker__week .react-datepicker__day {
    font-size: 14px;
    padding: 0 7px;
    margin: 0;
  }

  .react-datepicker .react-datepicker__navigation {
    width: 28px;
    height: 28px;
  }
}
.header {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  grid-gap: 30px;
  gap: 30px;
  width: 80vw;
  max-width: 1280px;
  margin: 0 auto;
}

.header__logo {
  width: 8em;
  object-fit: cover;
  margin: 1em 0 0;
}

@media (max-width: 821px) {
  .header {
    justify-content: center;
  }

  .header__logo {
    display: none;
    margin: 2em 0;
  }

  .header__logo_type_active {
    display: block;
  }
}

@media (max-width: 540px) {
  .header__logo {
    width: 6em;
    margin: 1em 0;
  }
}
.navigation {
  display: none;
  align-items: center;
}

.navigation_type_header {
  display: flex;
}

.navigation_type_footer {
  display: none;
}

.navigation__menu-links {
  display: flex;
  list-style: none;
  margin: 2.5em 0 2em;
  padding: 0;
  grid-gap: 15px;
  gap: 15px;
}

.navigation__menu-link {
  color: rgba(80, 80, 80, 1);
  font-weight: 500;
  font-size: 1.9em;
  line-height: 1;
  text-decoration: none;
}

.navigation__menu-link_type_active {
  color: #19b2e6;
}

.navigation__menu-img {
  display: none;
}

@media (max-width: 821px) {
  .navigation_type_header {
    display: none;
  }

  .navigation_type_footer {
    display: flex;
    justify-content: center;
    z-index: 200;
    width: 100%;
    max-width: 821px;
    background-color: rgba(255, 255, 255, 1);
  }

  .navigation__menu-links {
    margin-bottom: 0;
    display: flex;
    justify-content: space-evenly;
    width: 100%;
    grid-gap: 0;
    gap: 0;
  }

  .navigation__menu-item {
    display: flex;
    width: 25vw;
    justify-content: center;
    border-top: .1em solid #19b2e6;
  }

  .navigation__menu-item_type_active {
    border-top: .6em solid #19b2e6;
    padding-top: .5em;
  }

  .navigation__menu-link {
    display: flex;
    flex-direction: column;
    grid-gap: .5em;
    gap: .5em;
    justify-content: center;
    align-items: center;
    font-size: 2em;
    padding-bottom: .5em;
  }

  .navigation__menu-img {
    display: block;
    object-fit: contain;
    width: 45px;
    height: 45px;
    padding-top: .6em;
  }
}

@media (max-width: 540px) {
  .navigation__menu-img {
    width: 30px;
    height: 30px;
  }

  .navigation__menu-link {
    font-size: 1.4em;
  }
}

@media (max-width: 320px) {
  .navigation__menu-img {
    width: 20px;
    height: 20px;
  }

  .navigation__menu-link {
    font-size: 1.1em;
  }
}
.footer {
  display: none;
}

@media (max-width: 821px) {
  .footer {
    display: flex;
    justify-content: center;
    flex-shrink: 0;
    width: 100%;
    /* position: fixed;
