.statistics {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  flex: 1 1;
  grid-gap: 50px;
  gap: 50px;
  width: 80vw;
  max-width: 1280px;
  margin: 0 auto;
}

.statistics__month {
  display: flex;
  flex-direction: row;
  align-items: center;
  grid-gap: 20px;
  gap: 20px;
  margin: 0;
  padding: 0;
}

.statistics__month-latest,
.statistics__month-next {
  border: none;
  background: url(data:image/png;base64,) no-repeat center / cover;
  width: 32px;
  height: 32px;
  margin: 0;
  padding: 0;
}

.statistics__month-latest:hover,
.statistics__month-next:hover {
  cursor: pointer;
}

.statistics__month-next {
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}

.statistics__month-name {
