.today {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  flex: 1 1;
  width: 100%;
  padding: 0;
  margin: o auto;
  align-items: center;
}

.today__title {
  margin: 0 0 .5em;
  padding: 0;
  font-size: 2.3em;
  line-height: 1;
  font-weight: 500;
}

.today__text {
  margin: 0 0 20px;
  padding: 0;
  font-size: 2.1em;
  font-weight: 500;
  line-height: 1;
}

@media (max-width: 821px) {
  .today {
    justify-content: space-evenly;
  }
}

@media (max-width: 540px) {
  .today__title {
    font-size: 1.6em;
  }

  .today__text {
    font-size: 1.4em;
    margin-bottom: 1em;
  }
}
.char {
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  padding: 0;
  align-items: flex-start;
}

.char__text-content {
  display: flex;
  align-items: center;
  margin-bottom: 1em;
  position: relative;
}

.char__title {
  margin: 0 .5em 0 0;
  padding: 0;
  font-size: 2em;
  line-height: 1;
  font-weight: 500;
}

.char__show-more-button {
  background: url(/static/media/icon-info.834cded7.svg) no-repeat center / cover;
  border: none;
  height: 3em;
  width: 3em;
  padding: 0;
  margin: 0;
}

.char__show-more-button:hover {
  cursor: pointer;
}

.char__buttons {
  display: flex;
  margin: 0;
  padding: 0;
  list-style: none;
  width: 100%;
  max-width: 90vw;
  justify-content: center;
}

.char:last-of-type {
  margin-bottom: 50px;
}

.char__button {
  display: block;
  margin: 0;
  padding: .9em 0;
  width: 100%;
  font-size: 1.6em;
  line-height: 1;
  font-weight: 400;
  border: none;
}

.char__button_type_active {
  background-color: #19b2e6;
}

.char__button:hover {
  cursor: pointer;
}

.char__item {
  display: block;
  width: 8vw;
  margin: 0;
  padding: 0;
  border: .2em solid #19b2e6;
  border-right: none;
}

.char__item:last-of-type {
  border-right: .2em solid #19b2e6;
  border-radius: 0 .3em .3em 0;
}

.char__item:first-of-type {
  border-radius: .3em 0 0 .3em;
  z-index: 100;
}

.char__popup {
  visibility: hidden;
}

.char__popup_opened {
  visibility: visible;
}

.char__popup-text {
  display: flex;
  position: absolute;
  width: 25em;
  background-color: rgba(230, 230, 230, 1);
  top: 36px;
  left: 0;
  border-radius: .5em;
  border: .05em solid rgba(0, 0, 0, 1);
  margin: 0;
  padding: .5em;
  box-shadow: 2px 2px 7px rgb(107, 105, 105);
  text-align: center;
  z-index: 201;
  font-size: 1.6em;
}

@media (max-width: 1024px) {
  .char__item {
    width: 10vw;
  }
}

@media (max-width: 821px) {
  .char:last-of-type {
    margin-bottom: 125px;
  }

  .char__item {
    width: 15vw;
  }

  .char__popup-text {
    width: 200px;
    left: 3.5em;
  }
}

@media (max-width: 540px) {
  .char:last-of-type {
    margin-bottom: 15px;
  }

  .char__title {
    font-size: 1.5em;
  }

  .char__show-more-button {
    width: 2em;
    height: 2em;
  }

  .char__buttons {
    margin: 0 0 1.5em;
    max-width: 80vw;
  }

  .char__button {
    font-size: 1.1em;
  }

  .char__text-content {
    align-self: flex-start;
  }

  .char__popup-text {
    width: 280px;
    left: 0;
    top: 2.2em;
    font-size: 1.2em;
  }
}

@media (max-width: 400px) {
  .char:last-of-type {
    margin-bottom: 15px;
  }

  .char__title {
    font-size: 1.3em;
  }

  .char__buttons {
    margin: 0 0 1em;
  }

  .char__text-content {
    margin-bottom: 0.5em;
  }
}


.auth {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  width: 100%;
  background-color: #1ba0cc;
}

.auth__content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  width: 100%;
  max-width: 400px;
  grid-gap: 45px;
  gap: 45px;
}

.auth__logo {
  width: 100%;
  max-width: 9em;
  object-fit: cover;
  border-radius: 50%;
}

.auth__form {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin: 0;
  padding: 0;
  grid-gap: 45px;
  gap: 45px;
  width: 100%;
  max-width: 400px;
  min-height: 52.9vh;
}

.auth__form-content {
  display: flex;
  flex-direction: column;
  grid-gap: 45px;
  gap: 45px;
}

.auth__form-title {
  font-size: 2.4em;
  font-weight: 500;
  line-height: 1;
  margin: 0;
  padding: 0;
  text-align: center;
  color: rgba(255, 255, 255, 1);
}

.auth__form-inputs {
  display: flex;
  flex-direction: column;
  grid-gap: 30px;
  gap: 30px;
  list-style: none;
  margin: 0;
  padding: 0;
}

.auth__form-input-content {
  display: flex;
  flex-direction: column;
  grid-gap: 5px;
  gap: 5px;
}

.auth__form-label {
  font-size: 1.4em;
  font-weight: 400;
  line-height: 1;
  color: rgba(255, 255, 255, 1);
}

.auth__form-input {
  font-size: 1.4em;
  font-weight: 400;
  line-height: 1;
  padding: 8px 0;
  border: none;
  border-bottom: 1px solid rgba(255, 255, 255, .5);
  background-color: inherit;
  transition: border-bottom .5s linear;
  color: rgba(255, 255, 255, 1);
}

input::-moz-placeholder {
  color: rgba(255, 255, 255, .6);
}

input::-webkit-input-placeholder {
  color: rgba(255, 255, 255, .6);
}

.auth__form-input:hover {
  border-bottom: 1px solid rgba(250, 250, 250, 1);
}

.auth__form-content-button {
  display: flex;
  flex-direction: column;
  grid-gap: 15px;
  gap: 15px;
}

.auth__form-button {
  width: 100%;
  max-width: 400px;
  color: rgba(255, 255, 255, 1);
  background-color: #0b85f7;
  min-height: 45px;
  border: 1px solid rgba(255, 255, 255, .3);
  font-size: 1.4em;
  line-height: 1;
  font-weight: 500;
  margin: 0;
  padding: 0;
}

.auth__form-button:hover {
  cursor: pointer;
}

.auth__form-text-content {
  display: flex;
  justify-content: center;
  grid-gap: 10px;
  gap: 10px;
}

.auth__form-text,
.auth__form-login {
  font-size: 1.4em;
  font-weight: 400;
  line-height: 1;
  margin: 0;
  padding: 0;
  color: rgba(250, 250, 250, 1);
}

.auth__form-login {
  font-weight: 500;
  color: rgb(248, 188, 59);
  text-decoration: none;
}

@media (max-width: 540px) {
  .auth__content {
    max-width: 300px;
    grid-gap: 7.8125vh;
    gap: 7.8125vh;
  }

  .auth__logo {
    max-width: 7em;
  }

  .auth__form {
    margin: 0 10px;
    max-width: 300px;
    min-height: 65vh;
  }

  .auth__form-content {
    grid-gap: 7.8125vh;
    gap: 7.8125vh;
  }

  .auth__form-inputs {
    grid-gap: 20px;
    gap: 20px;
  }

  .auth__form-button {
    max-width: 300px;
  }
}

@media (max-width: 320px) {
  .auth__content {
    max-width: 240px;
  }

  .auth__logo {
    max-width: 6em;
  }

  .auth__form {
    max-width: 240px;
  }

  .auth__form-button {
    max-width: 240px;
  }
}

